// Open Sans Light
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Light/OpenSans-Light_gdi.eot");
  src: url("../fonts/OpenSans-Light/OpenSans-Light_gdi.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/OpenSans-Light/OpenSans-Light_gdi.woff") format("woff"),
    url("../fonts/OpenSans-Light/OpenSans-Light_gdi.ttf") format("truetype"),
    url("../fonts/OpenSans-Light/OpenSans-Light_gdi.otf") format("opentype"),
    url("../fonts/OpenSans-Light/OpenSans-Light_gdi.svg#OpenSans-Light")
      format("svg");
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-2212;
}

// Open Sans Regular
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular/OpenSans_gdi.eot");
  src: url("../fonts/OpenSans-Regular/OpenSans_gdi.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/OpenSans-Regular/OpenSans_gdi.woff") format("woff"),
    url("../fonts/OpenSans-Regular/OpenSans_gdi.ttf") format("truetype"),
    url("../fonts/OpenSans-Regular/OpenSans_gdi.otf") format("opentype"),
    url("../fonts/OpenSans-Regular/OpenSans_gdi.svg#OpenSans-Regular")
      format("svg");
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-25CA;
}
